<template>
  <div class="prompt-display">
    <h2>üßë ChatGPT„Å´Ë≥™Âïè„Åó„Å¶„Åø„Çà„ÅÜ</h2>
    <div class="input-group">
      <input
        class="input"
        placeholder="„Åì„Åì„Å´Ë≥™Âïè„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ"
        v-model="state.prompt"
        @keyup.enter="askChatGPT"
      />
      <button @click="askChatGPT" class="btn">ÈÄÅ‰ø°</button>
    </div>
    <div class="response">
      <pre>{{ state.response }}</pre>
    </div>
  </div>
</template>

<script setup>
import { reactive } from "vue";
import axios from "axios";

const state = reactive({
  prompt: "",
  response: "‚úÖ ChatGPT„ÅÆÂõûÁ≠î„Åå„Åì„Åì„Å´Ë°®Á§∫„Åï„Çå„Åæ„Åô„ÄÇ",
  isCollapsed: false,
});

const API_KEY = process.env.VUE_APP_API_KEY;
const http = axios.create({
  baseURL: "https://api.openai.com/v1/",
  headers: {
    "Content-Type": "application/json",
    Authorization: `Bearer ${API_KEY}`,
  },
});

async function askChatGPT() {
  if (!state.prompt.trim()) {
    state.response = "‚ö†Ô∏è Ë≥™Âïè„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ";
    return;
  }

  try {
    const result = await makeRequestWithRetry(
      "chat/completions",
      {
        model: "gpt-3.5-turbo",
        messages: [{ role: "user", content: state.prompt }],
        max_tokens: 256,
        temperature: 0.7,
      },
      3,
      2000
    ); // ÊúÄÂ§ß3Âõû„ÅÆ„É™„Éà„É©„Ç§„ÄÅ„É™„Éà„É©„Ç§ÈñìÈöî2000„Éü„É™Áßí
    state.response = result.data.choices[0].message.content;
  } catch (error) {
    console.error("ChatGPT„Åã„Çâ„ÅÆÂøúÁ≠î„ÅÆÂèñÂæó„Å´Â§±Êïó„Åó„Åæ„Åó„Åü:", error);
    if (error.response && error.response.data && error.response.data.error) {
      state.response = `‚ö†Ô∏è „Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü: ${error.response.data.error.message}. Ë©≥Á¥∞„ÅØOpenAI„ÅÆ„Éâ„Ç≠„É•„É°„É≥„Éà„ÇíÂèÇÁÖß„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ`;
    } else {
      state.response =
        "‚ö†Ô∏è ‰∏çÊòé„Å™„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ„Çµ„Éù„Éº„Éà„Å´ÈÄ£Áµ°„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ";
    }
  }
}

async function makeRequestWithRetry(url, data, retries, delay) {
  for (let i = 0; i <= retries; i++) {
    try {
      return await http.post(url, data);
    } catch (error) {
      if (i < retries && error.response && error.response.status === 429) {
        await new Promise((resolve) =>
          setTimeout(resolve, delay * Math.pow(2, i))
        ); // ÊåáÊï∞ÁöÑ„Éê„ÉÉ„ÇØ„Ç™„Éï
      } else {
        throw error;
      }
    }
  }
}
</script>

