<template>
  <div class="home">
    <VaAlert color="primary" border="top" border-color="primary" class="mb-6">
      Chatting Room
    </VaAlert>
    <div class="chat-container">
      <!-- „ÉÅ„É£„ÉÉ„Éà„É°„ÉÉ„Çª„Éº„Ç∏„ÅÆ„É™„Çπ„Éà„Å®„É°„ÉÉ„Çª„Éº„Ç∏ÂÖ•Âäõ„Éï„Ç£„Éº„É´„Éâ„ÇíÂåÖÂê´„Åô„Çã„Ç≥„É≥„ÉÜ„Éä -->
      <div class="chat-section">
        <message-list :messages="messages"></message-list>
        <message-input @send="addMessage"></message-input>
      </div>
      <!-- „Éó„É≠„É≥„Éó„ÉàË°®Á§∫ÈÉ®ÂàÜ -->
      <prompt-display></prompt-display>
    </div>
  </div>
</template>

<script>
// import dummyData from '@/dummyData';
import MessageList from "@/components/MessageList.vue";
import MessageInput from "@/components/MessageInput.vue";
import PromptDisplay from "@/components/PromptDisplay.vue";
import axios from "axios";

export default {
  name: "HomePage",
  components: {
    MessageList,
    MessageInput,
    PromptDisplay,
  },
  mounted() {
    this.fetchMessages();
  },
  methods: {
    addMessage(newMessage) {
      const username = localStorage.getItem("username");
      axios
        .post(process.env.VUE_APP_BASE_URL + "messages/", {
          username: username,
          message: newMessage,
        })
        .then(() => {
          console.log("Message posted successfully");
        })
        .catch((error) => {
          console.error("Failed to post message:", error);
          console.error("Detailed error:", error.response.data); // „Ç®„É©„Éº„ÅÆË©≥Á¥∞ÊÉÖÂ†±„Çí„É≠„Ç∞„Å´Ë®òÈå≤
          alert(`Error: ${error.response.data.message}`);
        });
      /*
      dummyData.messages.push({
        id: dummyData.messages.length + 1,
        user: 'currentUser',
        text: newMessage,
        icon: 'mdi-send'
      });
      */
    },
    fetchMessages() {
      axios
        .get("/api/messages")
        .then((response) => {
          this.messages = response.data;
        })
        .catch((error) => {
          console.error("Error fetching messages:", error);
        });
    },
  },
};
</script>

